<script>
export default {
  name: "List",
  functional: true,
  props: {
    ordered: {
      type: Boolean,
      default: false,
    },
  },
  render: function (createElement, context) {
    const listTag = context.props.ordered ? "ol" : "ul";
    return createElement(
      listTag,
      {
        slot: "list-items",
      },
      context.slots()["list-items"]
    );
  },
};
</script>
